<template>
  <div class="bg"></div>
</template>

<style>
.bg {
  background-image: url('../assets/images-light/timber.png');
  /*background-color: #415385;*/
  width: 100%;
  top:0px; 
  bottom: 0px;
  height: 100%;
  position: absolute;
  left: 0;
  background-position: center center;
  background-repeat: repeat-y;
  background-attachment: fixed;
  background-size: cover !important;
  z-index: -3;
}
</style>

<script>

export default {
    name: 'background',
    data () {
        return {
        }
    }
}
</script>


