<template>
    <v-container absolute fluid class="pa-0">
            <v-row>
            <v-col>
            <v-layout align-center column justify-center class="mt-5" data-aos="zoom-in">
                    <h2 class=" font-weight mt-5 " style="font-size:35px;">Projects</h2>
                    <v-divider></v-divider>
                    <p class="font-weight-light ">Here are a few projects I have built recently. </p>
            </v-layout>
            <v-row>
                <v-col
                    v-for="project in projects"
                    :key="project.title"
                    @mouseover="project.hover = true"
                    @mouseleave="project.hover = false"
                    :class="{'hover': project.hover}"
                > 
                    <v-card 
                    flat 
                    v-if="$resize && $mq.above(1025)" 
                    color="rgba(0, 0, 0, 0)"
                    > 
                    <v-img :src="project.image" data-aos="zoom-out">
                        <transition name="fade">
                            <v-overlay 
                            v-bind:id="project.id"
                            v-if="project.hover" 
                            absolute
                            opacity="0"
                            data-aos="flip-down" data-aos-duration="1000"
                            >
                            <!--Hover transitions-->
                            <v-container>
                                <v-layout fill-height>
                                    <v-flex xs12 align-end flexbox 
                                    >
                                        <v-btn class="ma-2" tile outlined color="orange darken-1"
                                        v-for="link in project.links"
                                        :key=link.name
                                        :href="link.url"
                                        target="_blank"
                                        >                                        
                                            <v-icon>{{ link.mdi }}</v-icon>{{ link.media }}
                                        </v-btn>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                            <div class="text-center" data-aos="fade-left" data-aos-duration="2000">
                                <h2>{{ project.title }}</h2>
                                <p class="pre-formatted">{{ project.description }}</p>
                            </div>
                            <div data-aos="fade-right" data-aos-duration="2000">
                            <v-chip
                            v-for="tool in project.tools"
                            :key="tool.toolName"
                            class="ma-2"
                            v-bind:color="tool.color"
                            text-color="white"
                            ><v-icon>{{ tool.icon}}</v-icon>
                            {{ tool.toolName}}
                            </v-chip>
                            </div>
                            </v-overlay>
                        </transition>
                    </v-img>
                    </v-card>
                    <v-card 
                    flat 
                    v-else 
                    data-aos="zoom-in" 
                    color="rgba(0, 0, 0, 0)">
                        <v-img
                        v-if="$resize && $mq.below(767)" 
                        :src="project.mobileImage"
                        data-aos="zoom-out" 
                        data-aos-duration="2000"
                        ></v-img>
                        <v-img
                        v-else
                        :src="project.image"
                        data-aos="zoom-out" 
                        data-aos-duration="2000"
                        ></v-img>
                        <!--Hover transitions-->
                        <div class="mobileInfo">
                            <v-container>
                                <v-layout fill-height>
                                    <v-flex xs12 align-end flexbox 
                                    >
                                        <v-btn class="ma-2" tile outlined color="orange darken-1"
                                        v-for="link in project.links"
                                        :key=link.name
                                        :href="link.url"
                                        target="_blank"
                                        data-aos="fade-right"
                                        >                                        
                                            <v-icon>{{ link.mdi }}</v-icon>{{ link.media }}
                                        </v-btn>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                            <div class="text-center" data-aos="fade-left">
                                <h2 >{{ project.title }}</h2>
                                <p>{{ project.description }}</p>
                            </div>
                            <div data-aos="fade-right">
                            <v-chip
                            v-for="tool in project.tools"
                            :key="tool.toolName"
                            class="ma-2"
                            v-bind:color="tool.color"
                            text-color="white"
                            ><v-icon>{{ tool.icon}}</v-icon>
                            {{ tool.toolName}}
                            </v-chip>
                            </div>  
                        </div>              
                    </v-card>
                </v-col>
            </v-row>
            </v-col>
            </v-row>
    </v-container>
</template>

<script>
export default {
    name: 'Projects',
    data () {
        return {
            active: false,
            projects: [
                {
                    title: 'Flatting App', 
                    image: require('../assets/images-light/Flatting App Cover.png'),
                    mobileImage: require('../assets/images-light//Flatting App Mobile.png'),
                    description: 'Prototype for a mobile app with the scheme of assisting the house-sharing lifestyle', 
                    id: 'left-text-box',
                    links: [{media: "Report", mdi: "mdi-file-pdf", url: "https://drive.google.com/file/d/18JGJ6FOXn6phA_eXPtQ3mgs_U6iBVEuY/view?usp=sharing"}, {media: "Research", mdi: "mdi-file-pdf", url: "https://drive.google.com/file/d/1xEKKII33oQP2517Fr024JeV6XYKW_dtp/view?usp=sharing"}], 
                    tools: [{toolName: "Xd", icon: "mdi-adobe", color: "purple darken-4"}]
                },
                {
                    title: 'Japan Day Brochure',
                    image: require('../assets/images-light/JD2019 Flyer Cover.png'), 
                    mobileImage: require('../assets/images-light/JD2019 Flyer Mobile.png'),
                    description: 'A double sided A3 Brochure/Pamphlet designer \n for Japan Day 2019', 
                    id: 'right-text-box',
                    links: [{media: "PDF", mdi:"mdi-file-pdf", url: "https://drive.google.com/file/d/1a1QBBsEvM8p_LlayLG3L1T9aOzJe-AuQ/view"}],
                    tools: [{toolName: "Ps", icon: "mdi-adobe", color: "light-blue darken-1"}, {toolName: "Ai", icon: "mdi-adobe", color: "amber darken-2"}, {toolName: "Id", icon: "mdi-adobe", color: "pink darken-1"}]
                },
                {
                    title: 'Venues API (Front-end)', 
                    image: require('../assets/images-light/Venue Site Cover.png'), 
                    mobileImage: require('../assets/images-light/Venue Site Mobile.png'),
                    description: 'Full-stack project of a Venues website', 
                    id: 'left-text-box',
                    links: [{media: "Github", mdi: "mdi-github-face", url: "https://github.com/ReiYoshi98/SENG365/tree/master/Travel-App-Frontend"}, {mdi: "mdi-eye", url: "https://drive.google.com/file/d/1uiLZl8zO_BAKVcbegPxvaGNWfqgaiotJ/view?usp=sharing"}], 
                    tools: [{toolName: "Vuejs", icon: "mdi-vuejs", color: "green darken-1"}, {toolName: "Nodejs", icon: "mdi-nodejs", color: "light-green darken-1"}]
                },
                {
                    title: 'Cashies App', 
                    image: require('../assets/images-light/Cashies Cover.png'),
                    mobileImage: require('../assets/images-light/Cashies Mobile.png'),
                    description: 'Mobile app with the scheme of building efficiency and fluidity to "Cashies" in New Zealand \n (Work in progress)', 
                    id: 'right-text-box',
                    tools: [{toolName: "Xd", icon: "mdi-adobe", color: "purple darken-4"}]
                },
                {
                    title: 'Ray Tracer', 
                    image: require('../assets/images-light/Ray Tracer.png'),
                    mobileImage: require('../assets/images-light/Ray Tracer Mobile.png'),
                    description: 'A Ray Tracer program built with C++ and OpenGl library, the tracer can \n' 
                    + 'handle various types of geometric objects, reflections, surfaces, and \n contain illumination features.', 
                    id: 'left-text-box',
                    links: [{media: "Report", mdi: "mdi-file-pdf", url: "https://drive.google.com/file/d/156ggTnH8wuNXfyq8yj9W8UqmUB3sgOyI/view?usp=sharing"}],
                    tools: [{toolName: "C++", color: "blue darken-1"}, {toolName: "OpenGl", color: "indigo lighten-3"}]
                }
            ]
        }
    },
    created () {
        this.projects.forEach((project) => {
            this.$set(project, 'hover', false)
        })
    }
}
</script>

<style scoped>

.v-application .mt-5 {
    margin-top: 30px!important;
}

.fade-enter-active {
  transition: opacity .5s
}

.fade-enter,
.fade-leave-active {
  opacity: 0
}

#left-text-box {
    position: relative;
    left: -200px;
    padding-top: 40px;
}

#right-text-box {
    position: relative;
    left: 250px;
    padding-top: 50px;
}

.mobileInfo {
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 50px;
}

.pre-formatted {
    white-space: pre;
}

</style>


