const animatedScrollOberserver = new IntersectionObserver(
    (entries, animatedScrollOberserver) => {
        entries.forEach((entry) => {
            if(entry.isIntersecting) {
                entry.target.classList.add('enter');
                animatedScrollOberserver.unobserve(entry.target);
            }
        });
    }
);

export default {
    bind(el) {
        el.classList.add('before-enter');
        animatedScrollOberserver.observe(el);
    }
}


