<template>
      <v-app-bar 
      class="navbar"
      :class="{ 'navbar--hidden': !showNavbar }"
      short 
      flat 
      fixed 
      app 
      color="rgba(38, 50, 56, 0.)" 
      dark>
        <v-spacer></v-spacer>
        <v-toolbar-items>
            <v-tabs fixed-tabs align-center background-color="rgba(0, 0, 0, 0)">
                <v-tab
                    ripple
                    v-scroll-to="'#home-main'"
                >
                <h3 v-if="$resize && $mq.above(480)">Home</h3>
                <v-icon v-else>mdi-home</v-icon>
                </v-tab>
                <v-tab
                    ripple
                    v-scroll-to="'#projects'"
                >
                <h3 v-if="$resize && $mq.above(480)">Projects</h3>
                <v-icon v-else>mdi-hexagon-multiple</v-icon>
                </v-tab>
                <v-tab
                    ripple
                    v-scroll-to="'#skills'"
                ><h3 v-if="$resize && $mq.above(480)">Skills</h3>
                <v-icon v-else>mdi-tools</v-icon>
                </v-tab>
            </v-tabs>
        </v-toolbar-items>
        <v-spacer></v-spacer>
      </v-app-bar>
</template>

<script>

export default {
    name: 'toolbar',
    data () {
        return {
          showNavbar: true,
          lastScrollPosition: 0
        }
    },
    mounted () {
      window.addEventListener('scroll', this.onScroll)
    },
    beforeDestroy () {
      window.removeEventListener('scroll', this.onScroll)
    },
    methods: {
      onScroll () {
        const currentScrollPosition = window.pageYOffset || document.documentElement.scrollTop
        if (currentScrollPosition < 0) {
          return
        }
        // Stop executing this function if the difference between
        // current scroll position and last scroll position is less than some offset
        if (Math.abs(currentScrollPosition - this.lastScrollPosition) < 60) {
          return
        }
        this.showNavbar = currentScrollPosition < this.lastScrollPosition
        this.lastScrollPosition = currentScrollPosition
      }
    }
}

</script>

<style>
.navbar {

  transform: translate3d(0, 0, 0) !important;
  transition: 0.1s all ease-out;
}
.navbar.navbar--hidden {
  box-shadow: none;
  transform: translate3d(0, -100%, 0) !important;
}
</style>


